anger';\r\n            }\r\n            if (tag === '预览版') {\r\n                return 'success';\r\n            }\r\n            return 'secondary';\r\n        },\r\n        match: function (tag) {\r\n            switch (tag) {\r\n                case '过时':\r\n                    let max = this.vmax;\r\n                    if (this.snapshot) {\r\n                        max--;\r\n                    }\r\n                    if (!this.properties[\"version-min\"]) {\r\n                        return false;\r\n                    }\r\n                    return this.properties[\"version-max\"] < max;\r\n                case '即将过时':\r\n                    if (!this.properties[\"version-min\"]) {\r\n                        return false;\r\n                    }\r\n                    return this.snapshot && this.properties[\"version-max\"] < this.vmax;\r\n                case '预览版':\r\n                    if (!this.properties[\"version-min\"]) {\r\n                        return false;\r\n                    }\r\n                    return this.snapshot && this.properties[\"version-min\"] === this.vmax;\r\n            }\r\n            return false;\r\n        }\r\n    },\r\n    computed: {\r\n        version: function () {\r\n            if (!this.properties[\"version-min\"]) {\r\n                return \"-\";\r\n            }\r\n            return \"1.\" + this.properties[\"version-min\"].toString() +\r\n                (this.properties[\"version-min\"] === this.properties[\"version-max\"] ?\r\n                \"\" : (\"-1.\" + this.properties[\"version-max\"].toString()));\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style>\r\n.hint {\r\n    padding: 0;\r\n    text-decoration: none;\r\n    display: inline;\r\n}\r\n\r\n.hint::after {\r\n    content: \"...\";\r\n}\r\n\r\n.hint:focus {\r\n    color: black;\r\n    text-decoration: none;\r\n    padding: 0;\r\n    display: inline;\r\n}\r\n\r\n.hint:focus::after {\r\n    content: \"\";\r\n}\r\n\r\n.authors {\r\n    display: none;\r\n}\r\n\r\n.hint:focus .authors {\r\n    display: inline;\r\n}\r\n\r\n.noselect {\r\n    -webkit-touch-callout: none; /* iOS Safari */\r\n    -webkit-user-select: none;   /* Safari */\r\n     -khtml-user-select: none;   /* Konqueror HTML */\r\n       -moz-user-select: none;   /* Firefox */\r\n        -ms-user-select: none;   /* Internet Explorer/Edge */\r\n            user-select: none;   /* Non-prefixed version, currently\r\n                                    supported by Chrome and Opera */\r\n}\r\n\r\n.title {\r\n    color: #28a745;\r\n}\r\n\r\n.thread {\r\n    margin-top: 0.25em;\r\n    margin-bottom: 0.25em;\r\n    padding-top: 0.25em;\r\n    padding-bottom: 0.25em;\r\n}\r\n\r\n.box {\r\n    margin-top: 0.5em;\r\n    margin-bottom: 0.5em;\r\n}\r\n\r\n.tag {\r\n    margin-right: 0.5em;\r\n    margin-bottom: 0.5em;\r\n}\r\n\r\n</style>"],"sourceRoot":""}]);

